import React from 'react';
import ReactEcharts from 'echarts-for-react';

class AreaChart1 extends React.Component {
    getOption = () => {
        return {
            tooltip: {
                trigger: 'axis'
            },
            xAxis: {
                type: 'category',
                data: ['Mon 09', 'Tue 10', 'Wed 11', 'Thu 12', 'Fri 13', 'Sat 14', 'Sun 15']
            },
            yAxis: {
                type: 'value'
            },
            color: '#58DECC',
            series: [{
                data: [11, 54, 30, 93, 47, 88, 0],
                type: 'line',
            }]
        }
    };

    render() {
        return <ReactEcharts option={this.getOption()} style={{height: '200px', width: '100%'}}/>;
    }
}

export default AreaChart1;