import React from 'react';
import ReactEcharts from 'echarts-for-react';

class PieDoughnutChart extends React.Component {
    getOption = () => {
        return {
            tooltip: {
                trigger: 'item',
                formatter: '{a} <br/>{b} : {c} ({d}%)'
            },
            legend: {
                orient: 'horizontal',
                x: 'left',
                data: ['BADREQUEST', 'UNAUTHORIZED', 'BADGATEWAY', 'FORMAT', 'OTHERS']
            },
            color: ['#f9dc2b', '#E1827A', '58DECC', '#243E8A', '#37474f'],
            calculable: true,
            series: [{
                name: 'Failure triggers',
                type: 'pie',
                radius: ['50%', '70%'],
                itemStyle: {
                    normal: {
                        label: {
                            show: false
                        },
                        labelLine: {
                            show: false
                        }
                    },
                    emphasis: {
                        label: {
                            show: true,
                            position: 'center',
                            textStyle: {
                                fontSize: '15',
                                fontWeight: 'regular'
                            }
                        }
                    }
                },
                data: [{
                    value: 33,
                    name: 'BADREQUEST'
                },
                    {
                        value: 31,
                        name: 'UNAUTHORIZED'
                    },
                    {
                        value: 23,
                        name: 'BADGATEWAY'
                    },
                    {
                        value: 13,
                        name: 'FORMAT'
                    },
                    {
                        value: 5,
                        name: 'OTHER'
                    }
                ]
            }]
        }
    };

    render() {
        return <ReactEcharts option={this.getOption()} style={{height: '250px', width: '100%',}}/>;
    }
}

export default PieDoughnutChart;